<template>
  <v-flex xs12>
    <v-data-table :headers="headers" :items="items" hide-actions class="elevation-1">
      <template slot="items" slot-scope="props">
        <td class="text-xs-left" v-for="(header, i) in headers.filter(h => h.value)" v-text="props.item[header.value]" :key="`car-${props.item.carId}-${i}`"/>
        <td class="justify-center layout px-0">
          <v-icon color="primary" @click="edit(props.item)" class="mx-2">mdi-pencil</v-icon>
          <v-icon color="primary" @click="remove(props.item)" class="mx-2">mdi-delete</v-icon>
        </td>
      </template>
    </v-data-table>

    <v-btn @click="edit({})" color="accent" fixed dark fab bottom right>
      <v-icon>mdi-plus</v-icon>
    </v-btn>
    <slot ref="edit" :dialog="dialog" :value="items[editedIndex] || {}" @input="save" @cancel="cancel">edit</slot>
  </v-flex>
</template>

<script>
export default {
  props: ['headers', 'items'],
  data: () => ({
    editedIndex: -1,
    dialog: false
  }),
  methods: {
    edit (item) {
      this.editedIndex = this.items.indexOf(item)
      this.dialog = true
    },
    save (item) {
      if (this.editedIndex > -1) {
        this.$set(this.items, this.editedIndex, item)
      } else {
        this.items.push(item)
      }
      this.$emit('save', item)
      this.dialog = false
    },
    remove (item) {
      if (
        confirm('Are you sure you want to delete this item?')
      ) {
        this.$emit('remove', item)
        this.items.splice(this.items.indexOf(item), 1)
      }
    },
    cancel (item) {
      this.dialog = false
    }
  },
  mounted () {
    this.$on('input', this.save)
    this.$on('cancel', this.cancel)
  },
  beforeDestroy () {
    this.$off('input', this.save)
    this.$off('cancel', this.cancel)
  }
}
</script>

